"use client"

import { useState } from "react"
import api from "@/utils/api"

export default function Login() {
    const [email, setEmail] = useState("");
    const [password, setPassword] = useState("");
    const [msg, setMsg] = useState("");


    const lidaComLogin = async (e: React.FormEvent) => {
        e.preventDefault();

        try {
            const res = await api.post("/login", { email, password });
            localStorage.setItem("token", res.data.token);;
            setMsg("Login executado")
        } catch {
            setMsg("Error ao fazer Login")
        }
           }

        return (
            <div className="flex flex-col items-center justify-center h-screen">
                <form onSubmit={lidaComLogin} className="flex flex-col gap-2">
                    <label >Email: </label>
                    <input
                        type="email"
                        placeholder="email"
                        value={email}
                        onChange={(e) => setEmail(e.target.value)}
                        className="border p-2"
                    />
                    <label>password :</label>
                    <input
                        type="password"
                        placeholder="password"
                        value={password}
                        onChange={(e) => setPassword(e.target.value)}
                        className="border p-2"
                    />

                    <button className="bg-amber-400 text-white rounded p-2">Login</button>
                </form>
                <p>{msg}</p>
            </div>
        )
 

}